import React from "react";

const CaseStudyCard = ({
  caseStudyImage,
  caseStudyTitle,
  caseStudyDescription
}) => {
  return (
    <div className="max-w-sm mr-auto ml-auto pb-2/3">
      <img className="h-full w-full object-cover pb-5" src={caseStudyImage} alt="Case study"/>
      <h6>
        <strong>{caseStudyTitle}</strong>
      </h6>
      <p>{caseStudyDescription}</p>
    </div>
  );
};

export default CaseStudyCard;
