import React, { Component } from "react";
import { Link } from "react-router-dom";
import IconComponent from "../components/IconComponent";
import "../css/base.css";

export default class About extends Component {
  render() {
    return (
      <div className="mx-5 lg:mx-0">
        <div className="about-section flex flex-wrap w-full ml-auto mr-auto lg:w-4/6">
          <div className="page-title pt-2 pb-20 w-full">
            <h4 className="float-right page-title-text">About</h4>
          </div>
          <section className="about-section mx-5 lg:m-0">
            <div className="about-section__content">
              <p className="text-left w-full sm:w-full md:w-full lg:w-full xl:w-5/6 body-text pb-5">
                One morning, when Gregor Samsa woke from troubled dreams, he
                found himself transformed in his bed into a horrible vermin. He
                lay on his armour-like back, and if he lifted his head a little
                he could see his brown belly, slightly domed and divided by
                arches into stiff sections.
              </p>
              <p className="text-left w-full sm:w-full md:w-full lg:w-full xl:w-5/6 body-text pb-5">
                One morning, when Gregor Samsa woke from troubled dreams, he
                found himself transformed in his bed into a horrible vermin. He
                lay on his armour-like back, and if he lifted his head a little
                he could see his brown belly, slightly domed and divided by
                arches into stiff sections.
              </p>
              <p className="text-left w-full sm:w-full md:w-full lg:w-full xl:w-5/6 body-text pb-5">
                One morning, when Gregor Samsa woke from troubled dreams, he
                found himself transformed in his bed into a horrible vermin. He
                lay on his armour-like back, and if he lifted his head a little
                he could see his brown belly, slightly domed and divided by
                arches into stiff sections.
              </p>
              <Link to="/about">
                <p className="text-left">more about michael ...</p>
              </Link>
              <div className="mt-10" />
              <div className="about-section__quote mb-20">
                <div
                  className="bg-gray-200 border-l-4 w-3/4 md:w-1/2 lg:w-1/2 sm:w-full ml-10 border-gray-500 text-gray-700 p-4"
                  role="alert"
                >
                  <p className="body-text">
                    Something not ideal might be happening.
                  </p>
                  <p className="font-bold">~ Be Warned</p>
                </div>
              </div>
            </div>
            <div className="mt-10 float-right">
              <Link to="/contact">
                <IconComponent />
              </Link>
            </div>
          </section>
        </div>
      </div>
    );
  }
}
