import React, { Component } from "react";
import "../css/base.css";

export default class Contact extends Component {
  render() {
    return (
      <div className="mx-5 lg:mx-0">
        <div className="contact-section flex flex-wrap w-full ml-auto mr-auto lg:w-4/6">
          <div className="page-title pb-20 pt-2 w-full">
            <h4 className="float-right page-title-text">Contact</h4>
          </div>
          <div className="w-full mb-10 text-center">
            <h3 className=" text-xl  large-heading">Reach out for a convo</h3>
          </div>
          <div className="flex flex-wrap w-full ml-auto mr-auto">
            <div className="w-full sm:w-full md:w-full lg:w-1/2 xl:w-1/2">
              <div className="contact-section__address text-center align-middle pt-10">
                <ul className="list-none ml-auto mr-auto">
                  <li className="text-base">mike.sarp.ky@gmail.com</li>
                  <li className="body-text">
                    <u>
                      <a target="_blank" href="#">
                        LinkedIn
                      </a>
                    </u>
                  </li>
                  <li className="body-text">
                    <u>
                      <a target="_blank" href="#">
                        Dribbble
                      </a>
                    </u>
                  </li>
                  <li className="body-text">
                    <u>
                      <a target="_blank" href="#">
                        Twitter
                      </a>
                    </u>
                  </li>
                  <li className="body-text">
                    <u>
                      <a target="_blank" href="#">
                        Résumé
                      </a>
                    </u>
                  </li>
                </ul>
                <p className="mt-20 invisible md:visible lg:visible small-text">
                  © 2019 Michael Sarpong
                </p>
              </div>
            </div>
            <div className="w-full sm:w-full md:w-full lg:w-1/2 xl:w-1/2 bg-gray-100">
              <div className="contact-section__form p-10">
                <form className="max-w-md ml-auto mr-auto">
                  <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                      * Full name
                    </label>
                    <input
                      className="w-full py-2 px-3"
                      type="text"
                      placeholder="Email"
                    />
                  </div>
                  <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                      * Email
                    </label>
                    <input className="w-full py-2 px-3" type="text" />
                  </div>
                  <div className="mb-6">
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                      * Message
                    </label>
                    <textarea
                      className="w-full py-2 px-3"
                      placeholder="Shoot your message here"
                    />
                  </div>
                  <div className="pb-10">
                    <button className="bg-gray-900 hover:bg-gray-700 text-white font-bold py-2 px-4 float-right">
                      Submit
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
