import React, { Component } from "react";
import { Link } from "react-router-dom";
import IconComponent from "../components/IconComponent";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";

import "../css/base.css";

export default class Home extends Component {
  render() {
    // function handleClick(e) {
    //   e.preventDefault();
    //   console.log("hello");
    // }
    return (
      <div className="home-section flex flex-wrap w-full ml-auto mr-auto lg:w-4/6">
        <section className="home-section mx-5 lg:m-0">
          <div className="home-section__content">
            <h3 className="text-left mb-5 sm:w-full md:w-full lg:w-2/3 mt-10 huge-heading">
              Michael is designing solutions for farmers at Agrocenta currently
            </h3>
            <p className="text-left w-full sm:w-full md:w-full lg:w-full xl:w-5/6 body-text pb-5">
              One morning, when Gregor Samsa woke from troubled dreams, he found
              himself transformed in his bed into a horrible vermin. He lay on
              his armour-like back, and if he lifted his head a little he could
              see his brown belly, slightly domed and divided by arches into
              stiff sections.
            </p>
            <Link to="/about">
              <p className="text-left">more about michael ...</p>
            </Link>
          </div>
          <div className="mt-20 float-right">
            <Link to="/about">
              <IconComponent iconName={faArrowRight} />
            </Link>
          </div>
        </section>
      </div>
    );
  }
}
