import React from "react";

const Footer = () => {
  const copyrightDate = new Date();
  return (
    <div className="footer-section text-center">
      <p className="mt-20 md:visible lg:visible small-text copyright">
        © {copyrightDate.getFullYear()} Michael Sarpong
      </p>
    </div>
  );
};
export default Footer;
