import React from "react";
import "../css/base.css";

const DribbbleCard = ({ dribbbleShot }) => {
  return (
    <div className="max-w-sm mr-auto ml-auto pb-2/3">
      <img className="h-full w-full object-cover" src={dribbbleShot} alt="Dribbble shot"/>
    </div>
  );
};

export default DribbbleCard;
